import React from "react";
// import axios from "axios";

import Store from "store"
import { Link, withRouter } from "react-router-dom";

class mainMenu extends React.Component {

	// Lets test this out, before we move this over to the App js file
	constructor(props) {
        super(props);
		this.chartReference = React.createRef();
		
        this.state = {
			something: null,
        };

		// We are going to have to redirect from here if there is no user...
	}

	componentDidMount() {
		console.log("Check for login...")

		if (this.props.currentUser === undefined) {
			this.props.history.push(this.props.reRouteTarget)
		}
	}
	
	getFromCookies = () => {
		Store.get('rememberSomething')
	};

	testButton = () => {
		console.log("Button!")
	}
	
	render() {
	
		return (
			<div className="mainMenu">

				<div className="container">
					<div className="row align-items-center my-5">
						<div className="col">
							Welcome to this barebones dashboard lookin thing!
						</div>
					</div>

					<div className="row align-items-center my-5">

						<div className="col">
							<div className="card">
								<div className="card-header">
									Write/Edit Journal
								</div>
								<div className="card-body">
									<p className="card-text">
										Click this button to write today's Journal
									</p>
									<Link className="btn btn-primary" to={this.props.journalWriteDirect}>
										Go!
									</Link>
								</div>
							</div>
						</div>

						<div className="col">
							<div className="card">
								<div className="card-header">
									View Past Journal
								</div>
								<div className="card-body">
									<p className="card-text">
										View the contents and AI result of your previous Journals
									</p>
									<Link className="btn btn-primary" to={this.props.journalReadDirect}>
										Go!
									</Link>
								</div>
							</div>
						</div>

						<div className="col">
							<div className="card">
								<div className="card-header">
									View Company Summary
								</div>
								<div className="card-body">
									<p className="card-text">
										View contents of Company Summaries, to which you belong.
									</p>
									<Link className="btn btn-primary" to={this.props.companyReadDirect}>
										Go!
									</Link>
								</div>
							</div>
						</div>

						<div className="col">
							<div className="card">
								<div className="card-header">
									Company Edit!
								</div>
								<div className="card-body">
									<p className="card-text">
										Edit Company Entiries to which you are Owner of
									</p>
									<Link className="btn btn-primary" to={this.props.companySettingDirect}>
										Go!
									</Link>
								</div>
							</div>
						</div>

					</div>
					
				</div>
			</div>
		);
	}
}

export default withRouter(mainMenu);